import type { Action, ConvertToPluginActionCreators, ConvertToPluginActionPredicates } from '@ali/4ever-cangjie';

export const SZ_SET = Symbol('SZ_SET');
export const SZ_ADJUST = Symbol('SZ_ADJUST');

export interface SetSZAction extends Action {
  type: typeof SZ_SET;
  payload: {
    sz: any;
  };
}

export interface AdjuestSZAction extends Action {
  type: typeof SZ_ADJUST;
  payload: {
    isIncreasingSz: boolean;
  };
}

export function setSz(sz): SetSZAction {
  return {
    type: SZ_SET,
    payload: { sz },
  };
}

export function adjustSz(isIncreasingSz: boolean): AdjuestSZAction {
  return {
    type: SZ_ADJUST,
    payload: { isIncreasingSz },
  };
}

export const isSetSz = (action: Action): action is SetSZAction => {
  return action.type === SZ_SET;
};

export const isAdjustSz = (action: Action): action is AdjuestSZAction => {
  return action.type === SZ_ADJUST;
};

export const actions = {
  setSz,
  adjustSz,
};

export const isActions = {
  isSetSz,
  isAdjustSz,
};

/** @deprecated */
export const DEPRECATED_ACTIONS = {
  SZ_SET,
  SZ_ADJUST,
  setSz,
  adjustSz,
};

declare module '@ali/4ever-cangjie' {
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions> {}
  interface PluginActionPredicates extends ConvertToPluginActionPredicates<typeof isActions> {}
}
